import React, { Component } from 'react';
import style from './Portfolio.module.css';
import {AiFillStar} from "react-icons/ai";
import {AiOutlinePlus} from "react-icons/ai";
import img1 from '../../img/portfolio/cabin.png'
import img2 from '../../img/portfolio/cake.png'
import img3 from '../../img/portfolio/circus.png'
import img4 from '../../img/portfolio/game.png'
import img5 from '../../img/portfolio/safe.png'
import img6 from '../../img/portfolio/submarine.png'
import PortfolioModals from '../PortfolioModals/PortfolioModals';
class Portfolio extends Component {
  render() {
    return (
      <section className={`py-5 ${style.portfolio}`} id="portfolio">
        <div className="d-flex flex-column align-items-center">
            <h2 className= {`fw-bold text-uppercase pt-2 ${style['title']}`} >portfolio</h2>
            <div className="d-flex align-items-center pb-2">
              <span className={style.line}></span>
              <span className={`mx-2 ${style.star}`}> <AiFillStar/> </span> 
              <span className={style.line}></span>
            </div>
        </div>
        <div className="container">
          <div className="row justify-content-center">

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img1} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop1"/>
                  </div>
                </div>
              </div>  
            </div>

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img2} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop2"/>
                  </div>
                </div>
              </div>  
            </div>

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img3} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop3"/>
                  </div>
                </div>
              </div>  
            </div>

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img4} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop4"/>
                  </div>
                </div>
              </div>  
            </div>

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img5} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop5"/>
                  </div>
                </div>
              </div>  
            </div>

            <div className={`col-sm-6 col-lg-4 mb-5  ${style.imgdiv}`}>   
              <div className="content position-relative">
                <img src={img6} className="img-fluid rounded"  alt="" />
                <div className={`${style.overlay} position-absolute rounded d-flex justify-content-center align-items-center`}>
                  <div className={style.content}>
                    <AiOutlinePlus className={` ${style.plusicon}`} data-bs-toggle="modal" data-bs-target="#staticBackdrop6"/>
                  </div>
                </div>
              </div>  
            </div> 
          </div>
        </div>
        
        <PortfolioModals/>
      </section>
    );
  }
}

export default Portfolio;