import React, { Component } from 'react';
import style from './StartSection.module.css';
import img from '../../img/avataaars.svg'
import {AiFillStar} from "react-icons/ai";

export default class StartSection extends Component {
  render() {
    return (
      <section className={`py-5 ${style['StartSection']}`}>
        <div className="container  d-flex flex-column align-items-center ">
          <img src={`${img} `} className={`${style.startimage}`} alt="" />

          <h1 className="text-white fw-bolder text-uppercase" >start bootstrap</h1>
          <div className="d-flex align-items-center pb-2">
            <span className={style.line}></span>
            <span className={`mx-2 ${style.star}`}> <AiFillStar/> </span> 
            <span className={style.line}></span>
          </div>
          <p className="text-white  text-capitalize">graphic artist - web designer - illustrator</p>
        </div>
      </section>
    )
  }
}
