import React, { Component } from 'react';
import style from  './Navbar.module.css';

class Navbar extends Component {
  render() {
    return (
      <div>
          <nav class={`navbar navbar-expand-lg navbar-dark py-2 fixed-top ${style.navbar}`}>
              <div class="container">
                <a class="navbar-brand text-uppercase " href="#">start bootstrap</a>
                
                <button className={`navbar-toggler d-block d-lg-none rounded px-1 py-2 d-flex align-items-center ${style['navbar-toggler']}`} type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <p className={` text-uppercase ${style['navbar-text']}`}>menu</p>
                  <span class={`navbar-toggler-icon ${style['font-size']}`}></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarSupportedContent">                
                  <ul class="navbar-nav ms-auto mb-2 mb-lg-0 py-1">

                    <li class="nav-item">
                      <a className={`nav-link text-uppercase   ${style['nav-link']}`} aria-current="page" href="#portfolio">portfolio</a>
                    </li>

                    <li className={`nav-item `}>
                      <a className={`nav-link text-uppercase  ${style['nav-link']}`} href="#">about</a>
                    </li>

                    <li className={`nav-item `}>
                      <a className={`nav-link text-uppercase ${style['nav-link']}`} href="#">contact</a>
                    </li>
                  
                  </ul>                  
                </div>
              </div>
        </nav>
      </div>
    );
  }
}

export default Navbar;